import React, {
    useEffect,
    useContext,
    useState,
    useRef,
    useReducer,
    useMemo,
    useCallback,
} from "react"
import ReactDOM from 'react-dom'
import {
    Preview, CodeEditor, ErrorBoundary,
    useValue,
    Context,
    Box,
    View,
    Button,
    Text,
    Image,
    ImageBackground,
    SearchBar,
    FlatList,
    TextInput,

} from "../components"

const scope = {
    React,
    Fragment: React.Fragment,
    Component: React.Component,
    render: rrender(),
    useEffect,
    useContext,
    useState,
    useRef,
    useReducer,
    useMemo,
    useCallback,
    useValue,
    Context,
    View,
    Button,
    Text,
    Image,
    ImageBackground,
    SearchBar,
    FlatList,
    TextInput,
}

function rrender() {
    return function render(element) {
        ReactDOM.render(element, document.getElementById("preview"))
    }
}

const initialCode = `
const App = () => <Text color="red">Hi Kids</Text>
`

const App = () => {
    const [code, setCode] = useState(initialCode)

    const handleEditorChange = (value) => {
        if (value !== code) {
            setCode(value)
        }
    };

    return (
        <div>
            <CodeEditor
                value={initialCode}
                onChange={handleEditorChange}
            />

            <Box
                position="absolute"
                top={0} right={0} width={300} height={600}
                justifyContent="center"
                alignItems="center"
                bg="transparent"
                backgroundImage="url('https://raw.githubusercontent.com/koenbok/Framer/master/extras/DeviceResources/iphone-6-silver.png')"
                backgroundSize="100% 100%"
                backgroundRepeat="no-repeat"
            >
                <ErrorBoundary>
                    <Preview code={code} scope={scope} />
                </ErrorBoundary>
            </Box>
        </div>
    );
}

export default App;

