export const Box = memo(styled('div')(props => _css(props)))

export const _css = p => {
    const _a = [
        "alignContent",
        "alignItems",
        "alignSelf",
        "aspectRatio",
        "backfaceVisibility",
        "backgroundColor",
        "borderBottomColor",
        "borderBottomLeftRadius",
        "borderBottomRightRadius",
        "borderBottomWidth",
        "borderColor",
        "borderLeftColor",
        "borderLeftWidth",
        "borderRadius",
        "borderRightColor",
        "borderRightWidth",
        "borderStyle",
        "borderTopColor",
        "borderTopLeftRadius",
        "borderTopRightRadius",
        "borderTopWidth",
        "borderWidth",
        "bottom",
        "color",
        "decomposedMatrix",
        "direction",
        "display",
        "elevation",
        "flex",
        "flexBasis",
        "flexDirection",
        "flexGrow",
        "flexShrink",
        "flexWrap",
        "fontFamily",
        "fontSize",
        "fontStyle",
        "fontVariant",
        "fontWeight",
        "height",
        "includeFontPadding",
        "justifyContent",
        "left",
        "letterSpacing",
        "lineHeight",
        "margin",
        "marginBottom",
        "marginHorizontal",
        "marginLeft",
        "marginRight",
        "marginTop",
        "marginVertical",
        "maxHeight",
        "maxWidth",
        "minHeight",
        "minWidth",
        "opacity",
        "overflow",
        "overlayColor",
        "padding",
        "paddingBottom",
        "paddingHorizontal",
        "paddingLeft",
        "paddingRight",
        "paddingTop",
        "paddingVertical",
        "position",
        "resizeMode",
        "right",
        "rotation",
        "scaleX",
        "scaleY",
        "shadowColor",
        "shadowOffset",
        "shadowOpacity",
        "shadowRadius",
        "textAlign",
        "textAlignVertical",
        "textDecorationColor",
        "textDecorationLine",
        "textDecorationStyle",
        "textShadowColor",
        "textShadowOffset",
        "textShadowRadius",
        "tintColor",
        "top",
        "transform",
        "transformMatrix",
        "translateX",
        "translateY",
        "width",
        "writingDirection",
        "zIndex",
        "border",
        "boxShadow",
        "flexFlow",
        "font",
        "textShadow",
        "textDecoration",
        "textDecorationLine",
        "transform",
        "bg",
        "size",
        "m", "mt", "mb", "ml", "mr", "mx", "my",
        "p", "pt", "pb", "pl", "pr", "px", "py",
        "backgroundImage",
        "backgroundSize",
        "backgroundPosition",
        "backgroundRepeat"
    ]

    const _c = k => {
        switch (k) {
            case "bg": return "backgroundColor";
            case "m": return "margin";
            case "mt": return "marginTop";
            case "mr": return "marginRight";
            case "mb": return "marginBottom";
            case "ml": return "marginLeft";
            case "mx": return "marginX";
            case "my": return "marginY";
            case "p": return "padding";
            case "pt": return "paddingTop";
            case "pr": return "paddingRight";
            case "pb": return "paddingBottom";
            case "pl": return "paddingLeft";
            case "px": return "paddingX";
            case "py": return "paddingY";
            default: return k;
        }
    }

    return Object.keys(p)
        .filter(key => _a.includes(key))
        .filter(key => p[key] !== true)
        .filter(key => p[key] !== null)
        .reduce((obj, key) => {
            if (key === "size") {
                obj["width"] = p[key]
                obj["height"] = p[key]
            } else {
                obj[_c(key)] = p[key];
            }
            return obj;
        }, {});
}

