export const Button = ({ children, color = "darkmagenta", fontSize, fontFamily, fontWeight = "bold", lineHeight, letterSpacing, textAlign, fontStyle, textShadow, ...props }) => {
    return (
        <Box as={N.TouchableOpacity} flexDirection="row" justifyContent="center" alignItems="center" {...props} >
            {React.Children.map(children, child => ReactIs.isElement(child) ? child : <Text
                color={color}
                fontFamily={fontFamily}
                fontSize={fontSize}
                fontWeight={fontWeight}
                lineHeight={lineHeight}
                letterSpacing={letterSpacing}
                textAlign={textAlign}
                fontStyle={fontStyle}
                textShadow={textShadow} >{child}</Text>)}
        </Box>
    )
}

//Context 
const RCtx = createContext();

export const Context = ({ children, value }) => <RCtx.Provider value={value}>{children}</RCtx.Provider>

export const useValue = () => useContext(RCtx)
//SearchBar

export const SearchBar = memo(({
    onSearch,
    ref,
    height = 32,
    margin = 2,
    color = "black",
    borderRadius = 5,
    bg = "white",
    cancelColor = "blue",
    placeholder = "search",
    ...props }) => {
    const [value, setValue] = useState("")
    const [iconVisible, setIconVisible] = useState(false)
    return (
        <View
            height={height}
            {...props}
            flexDirection="row"
            alignItems="stretch"
        >
            <View
                flex={1}
                flexDirection="row"
                alignItems="center"
                justifyContent="space-between"
                borderRadius={borderRadius}
                bg={bg}
            >

                <Text color={color ? transparentize(0.5, color) : "grey"} fontSize={22} mb={4}> ⌕ </Text>
                <TextInput
                    ref={ref}
                    value={value}
                    onChangeText={text => {
                        setValue(text)
                        onSearch(text)
                    }}
                    onFocus={() => setIconVisible(true)}
                    onBlur={() => setIconVisible(false)}
                    multiline={false}
                    placeholder={placeholder}
                    placeholderTextColor={color ? transparentize(0.5, color) : "grey"}
                    backgroundColor="transparent"
                    flex={1}
                    color={color}
                ></TextInput>
                {iconVisible ? (<TouchableOpacity onPress={() => {
                    setValue("");
                    onSearch("")
                }
                }>
                    <Text color={color ? transparentize(0.5, color) : "grey"} fontSize={18}> ⊗ </Text>
                </TouchableOpacity>) : null}
            </View>
            {iconVisible ? (<Button
                onPress={() => {
                    setValue("")
                    onSearch("")
                    N.Keyboard.dismiss();
                    setIconVisible(false);
                }}
                color={cancelColor ? cancelColor : "blue"}
                justifyContent="center"
                fontSize={20}
                fontWeight="200"
                m={2}
            >cancel</Button>) : null}
        </View>
    )
})

//Preview
const usePreview = ({ code, scope }) => {
    const transformCode = code => {
        return transform(code, {
            presets: [['stage-0', { decoratorsLegacy: true }], 'react'],
        }).code
    }

    let sC = `
    const Root = () => <View bg="white" width={264} height={466} mt={67} ml={18.4} ><App/></View>;
    render(<Root />);
    `
    useEffect(() => {
        let transformed = transformCode(code + sC)
        const func = new Function(...Object.keys(scope), transformed)
        func(...Object.values(scope))
    }, [code])
}

export const Preview = ({ code, scope, ...props }) => {
    usePreview({ code, scope })
    return <div {...props} id="preview" />
}


export class CodeEditor extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: '',
        };
    }

    static getDerivedStateFromProps(props, state) {
        if (props.value !== state.prevValueProp) {
            return { value: props.value, prevValueProp: props.value };
        }
        return null;
    }

    updateContent = (ev, value) => {
        this.setState({ value }, () => {
            if (this.props.onChange) {
                this.props.onChange(this.state.value);
            }
        });
    };

    render() {
        // eslint-disable-next-line no-unused-vars
        const {
            value,
            onChange,
            ...rest
        } = this.props;

        return (
            <ControlledEditor
                height="95vh"
                theme="dark"
                value={value}
                onChange={this.updateContent}
                language="javascript"
                options={{
                    fontSize: 18
                }}
                {...rest}
            />
        );
    }
}

export class ErrorBoundary extends Component {
    constructor(props) {
        super(props);
        this.state = { error: null, errorInfo: null };
    }

    componentDidCatch(error, errorInfo) {
        // Catch errors in any components below and re-render with error message
        this.setState({
            error: error,
            errorInfo: errorInfo
        })

        setTimeout(function () {
            this.setState({
                error: null,
                errorInfo: null
            })
        }.bind(this), 3000);
    }

    render() {
        if (this.state.errorInfo) {
            // Error path
            return (
                <Box bg="white" width={264} height={466} mt={67} ml={18.4}>
                    <Box as={"h2"} color="red">Error</Box>
                    <Box color="darkblue">
                        {this.state.error.message.toString()}
                    </Box>
                </Box>
            );
        }
        // Normally, just render children
        return this.props.children;
    }
}